@import "../../style.scss";

.post{
    @include themify($themes){

        -webkit-box-shadow: 0px 0px 25px -10px rgba(0, 0, 0, 0.38);
        -moz-box-shadow: 0px 0px 25px -10px rgba(0, 0, 0, 0.38);
        box-shadow: 0px 0px 25px -10px rgba(0, 0, 0, 0.38);
        border-radius: 20px;
        background-color: themed("bgSoft");
        color: themed("textColor");
        

    .container{
        padding: 40px;
        
    }
    .user{
        display: flex;
        align-items: center;
        justify-content: space-between;
        position: relative;
        .userInfo{
            display: flex;
            gap: 20px;
            
            img{
                width: 40px;
                height: 40px;
                border-radius: 50%;
                object-fit: cover;
            }
            .details{
                display: flex;
                flex-direction: column;
                .name{
                    font-weight: 500;
                }
                .date{
                    font-size: 12px;
                }
            }
        }
        button {
            position: absolute;
            top: 30px;
            right: 0;
            border: none;
            background-color: #f0544f;
            padding: 5px;
            cursor: pointer;
            color: white;
          }
    }
    .content{
       margin: 20px 0px; 
       padding: 0;
       height: 100%;
       
       img{
        width: 100%;
        max-height: 800px;
        object-fit: contain;
        margin-top: 20px;
       }
    }
    .info{
        display: flex;
        align-items: center;
        gap: 20px;

        .item{
            
            display: flex;
            align-items: center;
            gap: 10px;
            cursor: pointer;
            font-size: 14px;
        }
    }
}
}
