.login{
height: 100vh;
background: url("https://images.vexels.com/media/users/3/144435/raw/9c5af286a9bf9bb8ef5240f179601e04-abstract-background-with-colorful-squares-and-lines.jpg");
background-size: cover;
display: flex;
align-items: center;
justify-content: center;
    .card{
        width: 30%;
        min-height: 600px;
        display: flex;
        justify-content: center;
        border-radius: 10px;
        background-color: rgba(255, 255, 255, 0.371);

        .right{
            padding: 50px;
            display: flex;
            flex-direction: column;
            gap: 30px;
            color: black;
            justify-content: center;
            h1{
                color: black;
                font-family: fantasy;
                font-size: 40px;
            }
            form{
                display: flex;
                
                flex-direction: column;
                gap: 30px;
                input{
                    border: none;
                    background-color: #e89ec15f;
                    padding: 8px 8px;
                    display: flex;
                    border-radius: 10px;
                    color: #000;
                }
                button{
                    width: 50%;
                    padding: 10px;
                    border: none;
                    background-color: #E89EC1;
                    color: black;
                    font-weight: bold;
                    cursor: pointer;
                    border-radius: 10px;
                }
                
            }
            a{
                color: rgb(81, 81, 252);
            }
            .or{
                display: flex;
                justify-content: center;
            }
            .button{
                display: flex;
                align-items: center;

                body { padding: 2em; }


                /* Shared */
                .loginBtn {
                  box-sizing: border-box;
                  position: relative;
                  width: 8em; 
                  margin: 0.2em;
                  padding: 0 15px 0 46px;
                  border: none;
                  text-align: left;
                  line-height: 34px;
                  white-space: nowrap;
                  border-radius: 0.2em;
                  font-size: 16px;
                  color: #FFF;
                }
                .loginBtn:before {
                  content: "";
                  box-sizing: border-box;
                  position: absolute;
                  top: 0;
                  left: 0;
                  width: 34px;
                  height: 100%;
                }
                .loginBtn:focus {
                  outline: none;
                }
                .loginBtn:active {
                  //box-shadow: inset 0 0 0 32px rgba(0,0,0,0.1);
                }
                
                
                /* Facebook */
                .loginBtn--facebook {
                  background-color: #4C69BA;
                  background-image: linear-gradient(#4C69BA, #3B55A0);
                  /*font-family: "Helvetica neue", Helvetica Neue, Helvetica, Arial, sans-serif;*/
                  text-shadow: 0 -1px 0 #354C8C;
                }
                .loginBtn--facebook:before {
                  border-right: #364e92 1px solid;
                  background: url('https://s3-us-west-2.amazonaws.com/s.cdpn.io/14082/icon_facebook.png') 6px 6px no-repeat;
                }
                .loginBtn--facebook:hover,
                .loginBtn--facebook:focus {
                  background-color: #5B7BD5;
                  background-image: linear-gradient(#5B7BD5, #4864B1);
                }
                
                
                /* Google */
                .loginBtn--google {
                  /*font-family: "Roboto", Roboto, arial, sans-serif;*/
                  background: #DD4B39;
                }
                .loginBtn--google:before {
                  border-right: #BB3F30 1px solid;
                  background: url('https://s3-us-west-2.amazonaws.com/s.cdpn.io/14082/icon_google.png') 6px 6px no-repeat;
                }
                .loginBtn--google:hover,
                .loginBtn--google:focus {
                  background: #E74B37;
                }
            }
        }
    }
}